.box{
 
  height: 20rem;
  width: 20rem;
  background-color: #FBAB7E;
  background-image: linear-gradient(62deg, #FBAB7E 0%, #F7CE68 100%);
  
border-radius:   37% 63% 39% 61% / 55% 41% 59% 45%  ;
animation: box 10s ease-in-out  infinite alternate;
}

.boxtransform{


  
  height: 50rem;
  width: 20rem;
  background-color: #f97b35;
background-image: linear-gradient(62deg, #f97b35 0%, #f2b627 100%);

border-radius:   37% 63% 39% 61% / 55% 41% 59% 45%  ;
 margin: 0 40%;

 animation: Scaling 1s ease-in 0s;
}
.boxtransform.frontImage1{

  animation: Scaling 1s ease-in 0s;

}


.frontCoin{

  height: 100vh;
  background-image: url(./Images//lofi2.jpg);
  background-size: cover;
}

.frontImage1{
  padding-left: 7rem;
  padding-top: 8rem;
  height: 9rem;
  width: 6rem;
  background-color: transparent;
}


.HomeCoin{
  display: grid;
  height: 100vh;
  width: 100%;
background-image: linear-gradient(45deg, #85FFBD 0%, #FFFB7D 100%);
background-color: rgba(212, 197, 197, 0);

  background-image: url(./Images/zoro3\ \(2\).png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: right;
  overflow:auto;
  background-attachment: fixed;
  animation: blazing 5s infinite alternate;
}

.frontButton{

  background: red;
}

.taskInput{

  position: fixed;
  bottom: 1rem;
  border-radius: .5rem;
  font-size: larger;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  margin-left: 15rem;
  height: 2rem;
  width: 30rem;
  background-color: rgba(255, 237, 237, 0);
  box-shadow: 0 0 10px hsla(0, 0%, 59%, 0.5), inset 0 0 10px rgba(0, 0, 0, 0.5);
}

.command{

  display: flex;
  align-items: center;
  margin-left: 1rem;
 align-self: flex-end;
height: 3rem;
width: 25rem;
text-align: center;
max-width: auto;
border:none;
font-size: .7rem;
color: white;
font-weight: 800;
margin-bottom: 2rem;
overflow: hidden;


text-transform: capitalize;
font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
background: rgb(227,170,31);
background: linear-gradient(103deg, rgba(227,170,31,0.9584659612517251) 20%, rgba(212,67,0,0.6808634375924192) 93%);
border-radius: .6rem ;
box-shadow: .4rem .6rem .7rem gray;
}
.taskButton{
  
  display: none;
  background: wheat;
  height: 2.5rem;
  width: 10rem;
  box-shadow: .2rem .2rem .2rem .2rem #97979780;
  font-size: 1.3rem;
  font-weight: 700;
  color: black;
}

.command.element{

  transform: scale(1.3x);
}

@media (max-width:426px) {
  .taskInput{
    margin-left: 4rem;
    margin-bottom: 2rem;
  }
  .command{
    width: 15rem;
    height:7rem;
    font-size: 1rem;

  }
  .box{
    height: 40rem;
    margin-left: 10rem;
  }
  .frontImage1{
    height: 30rem;
    width: 10rem;
  }
  .frontButton{
    width: 19rem;
  }
  .taskButton{
    margin-right: 10rem;
  }
  .butt{
    display: none;
  }
  .taskButton{
    display: block;
   margin-left: 35rem;
   margin-bottom: 2rem;
  }
}


@keyframes Scaling {
  from{
    transform: scale(1);
  }
  to{
    transform: scale(4);
    background-color: #f97b35;

    background-image: linear-gradient(62deg, #f97b35 0%, #f2b627 100%);
    
  }
}

@keyframes Move {
  from{
  bottom: 0;
  transform: rotateY(30deg);
  }
  to{
   
   transform: rotateX(180deg);
  }
}

@keyframes box {
  from{
    border-radius:   37% 63% 39% 61% / 55% 41% 59% 45%  ;

  }
  10%{
  border-radius:   45% 55% 45% 55% / 37% 32% 68% 63% ;

  }
  30%{
    border-radius:14% 86% 18% 82% / 73% 32% 68% 27% 
  }
  60%{
    border-radius:14% 86% 6% 94% / 0% 94% 6% 100% 
  }
  100%{
    border-radius: 77% 23% 73% 27% / 0% 94% 6% 100% ;
  }
}



@keyframes blazing {
  0% {
    background-color: #f2aeae; /* Red */
  }
  25% {
    background-color: #efb38a; /* Orange */
  }
  50% {
    background-color: #e2cc76; /* Yellow */
  }
  75% {
    background-color: #9fdc8b; /* Green */
  }
  100% {
    background-color: #95c6e7; /* Blue */
  }
}
